import React, { useRef, useState } from "react";
import css from "./Contact.module.css";
import { Container, Row, Col } from "reactstrap";
import { FaEnvelope } from "react-icons/fa";
import { FaPhoneSquare } from "react-icons/fa";
import { useHistory } from "react-router-dom";
import Notification from "../Notification";

function Contact() {
  const [show, setShow] = useState(false);
  const history = useHistory();
  const nameRef = useRef();
  const numberRef = useRef();
  const emailRef = useRef();
  const messageRef = useRef();

  function submitData(e) {
    e.preventDefault();
    const nameInputRef = nameRef.current.value;
    const numberInputRef = numberRef.current.value;
    const emailInputRef = emailRef.current.value;
    const messageInputRef = messageRef.current.value;

    const contactMessage = {
      Name: nameInputRef,
      Number: numberInputRef,
      Email: emailInputRef,
      Message: messageInputRef,
    };
    fetch(
      "https://portfolio-messages-ab437-default-rtdb.europe-west1.firebasedatabase.app/messages.json",
      {
        method: "POST",
        body: JSON.stringify(contactMessage),
        headers: {
          "Content-Type": "application/json",
        },
      }
    ).then(() => {
      setShow(true);
      setTimeout(() => {
        history.replace("/");
      }, 3500);
    });
  }

  return (
    <div className={css.contactMe}>
      <h1>Contact Me</h1>
      <form onSubmit={submitData} className={css.contact}>
        <div className={css.inputContainer}>
          <input type="text" required id="title" ref={nameRef} />
          <label htmlFor="name">Name</label>
        </div>
        <div className={css.inputContainer}>
          <input type="number" required id="title" ref={numberRef} />
          <label htmlFor="phone">Phone Number</label>
        </div>
        <div className={css.inputContainer}>
          <input type="email" required id="email" ref={emailRef} />
          <label htmlFor="email">Email</label>
        </div>
        <div className={css.inputContainer}>
          <textarea
            cols=""
            rows="5"
            type="text"
            required
            id="message"
            ref={messageRef}
          />
          <label htmlFor="phone">Message</label>
        </div>

        <button className="btn btn-success">Send</button>
        {show === true ? <Notification /> : ""}
      </form>

      <Container className={css.getinTouch}>
        <h3>Get In Touch</h3>
        <Row>
          <Col md="6">
            <div className={css.details}>
              <a href="mailto:ezeifeanyichukwu21@gmail.com">
                <div>
                  <FaEnvelope className={css.icon} />
                </div>
                Email Me
              </a>
            </div>
          </Col>
          <Col md="6">
            <div className={css.details}>
              <a href="tel:+2348067749483">
                <div>
                  <FaPhoneSquare className={css.icon} />
                </div>
                Call/Text Me
              </a>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default Contact;
