import React, { useState } from "react";
import { Collapse, Navbar, NavbarToggler, Nav, NavItem } from "reactstrap";
import { Link } from "react-router-dom";
import classes from "./Navbar.module.css";

const NavBar = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div>
      <Navbar light className={classes.navAdjust} expand="md" fixed="top">
        <Link to="/" className={classes.brand} style={{ fontSize: "1.7rem" }}>
          IFEANYI
        </Link>

        <NavbarToggler onClick={toggle} />
        <Collapse className={classes.open} isOpen={isOpen} navbar>
          <Nav onClick={toggle} className={classes.adjust} navbar>
            <NavItem>
              <Link to="/">Home</Link>
            </NavItem>
            <NavItem>
              <Link to="/about-me">About Me</Link>
            </NavItem>
            <NavItem>
              <Link to="/projects">Projects</Link>
            </NavItem>
            <NavItem>
              <Link to="/contact">Contact Me</Link>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  );
};

export default NavBar;
