import { Route, Switch } from "react-router-dom";
import Footer from "./Components/Footer";
import HomePage from "./Components/Pages/HomePage";
import AboutMe from "./Components/Pages/AboutMe";
import Contact from "./Components/Pages/Contact";
import NavBar from "./NavBar/Navbar";
import { ThemeProvider } from "styled-components";
import { GlobalStyles } from "./GlobalStyles";
import { useState } from "react";
import Projects from "./Components/Pages/Projects";

function App() {
  let [mode, setMode] = useState();

  function changeMode() {
    if (mode === "dark") {
      setMode("light");

      localStorage.setItem("nEvbTUxraS", "light");
    } else {
      setMode("dark");
      localStorage.setItem("nEvbTUxraS", "dark");
    }
  }

  function modeStatus() {
    if (localStorage.getItem("nEvbTUxraS") !== null) {
      mode = localStorage.getItem("nEvbTUxraS");
    } else {
      mode = "light";
      localStorage.setItem("nEvbTUxraS", "light");
    }
    return mode;
  }

  return (
    <div>
      <ThemeProvider theme={{ mode: modeStatus() }}>
        <GlobalStyles />
        <NavBar />
        <Switch>
          <Route path="/" exact>
            <HomePage
              button={changeMode}
              checked={mode === "dark" ? true : false}
            />
          </Route>
          <Route path="/about-me">
            <AboutMe />
          </Route>
          <Route path="/projects">
            <Projects />
          </Route>
          <Route path="/contact">
            <Contact />
          </Route>
        </Switch>
        <Footer />
      </ThemeProvider>
    </div>
  );
}

export default App;
