import React from "react";
import css from "../Components/ToggleButton.module.css";
import { FaSun, FaMoon } from "react-icons/fa";

function ToggleButton(props) {
  return (
    <div>
      <input
        onClick={props.toggler}
        type="checkbox"
        className={css.checkbox}
        id="chk"
        defaultChecked={props.inputChecked}
      />
      <label className={css.label} htmlFor="chk">
        <FaSun style={{ fontSize: "0.5rem" }} />
        <FaMoon style={{ fontSize: "0.5rem" }} />
        <div className={css.ball}></div>
      </label>
    </div>
  );
}

export default ToggleButton;
